import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import Style from './style';

interface Props extends WithStyles<typeof Style> {
	[name: string]: any;
	image: string;
	name: string;
	designation: string;
	description: string;
	smallCard?: boolean;
	padding?: boolean;
}
/**
 * Card for founders section.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class Card extends React.PureComponent<Props> {
	componentDidMount() {}

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const {
			classes,
			image,
			name,
			designation,
			description,
			smallCard = false,
			padding = false
		} = this.props;
		return (
			<div className={clsx({ [classes.card]: !smallCard && name !== '' })}>
				<img
					src={image}
					alt="card"
					className={clsx(classes.heroImage, {
						[classes.smallCard]: smallCard,
						[classes.padding]: padding
					})}
				/>
				<div className={clsx({ [classes.cardInfo]: name !== '' })}>
					<div className={classes.mediumFont}>{name}</div>
					<div className={classes.smallFont}>{designation}</div>
					<div className={clsx(classes.description, classes.smallerFont)}>
						{description}
					</div>
				</div>
			</div>
		);
	}
}

export default withStyles(Style, { name: 'DmartCorpFreshCard' })(Card);
