import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import scrollIntoView from 'scroll-into-view';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepConnector from '@material-ui/core/StepConnector';
import { StepIconProps } from '@material-ui/core/StepIcon';
import FiberManualRecordOutlinedIcon from '@material-ui/icons/FiberManualRecordOutlined';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import VisibilityDetector from 'react-visibility-detector';
import { get } from 'lodash';
import Carousel from 'react-material-ui-carousel';
import Loadable from '@loadable/component';
import {
	ProgrammesContent,
	Resources,
	InitiativesContent,
	CarouselImages
} from '../../constants/resources';
import Style from './style';
import { RootState } from '../../reducers';
import { connect } from 'react-redux';
import Card from '../../components/Card';

const Tile = Loadable(() => import('../../components/Tile'));
// const Card = Loadable(() => import('../../components/Card')); //Because the Carousel needs it 
const DesktopCard = Loadable(()=>import('../../components/DesktopCard')) ;
const ImageInfoBlocks = Loadable(()=>import('../../components/ImageInfoBlocks')) ;
const Footer = Loadable(() => import('../../components/Footer'));
interface State {
	step: string;
	stepping: boolean;
}
const steps = [
	{ name: 'Overview', id: 'overview' },
	{ name: 'Ongoing Programmes', id: 'programmes' },
	{ name: 'New Initiative', id: 'initiative' }
];
const QontoConnector = withStyles({
	alternativeLabel: {
		left: 'calc(-50% + 16px)',
		right: 'calc(50% + 16px)',
		top: 8
	},
	active: {
		'& $line': {
			borderColor: '#979797'
		}
	},
	completed: {
		'& $line': {
			borderColor: '#979797'
		}
	},
	line: {
		borderColor: '#979797',
		borderTopWidth: 1,
		borderRadius: 1
	}
})(StepConnector);
interface Props extends WithStyles<typeof Style> {
	[name: string]: any;
	isDesktopView: boolean;
}
/**
 * SocialOutreach page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class SocialOutreach extends React.PureComponent<Props, State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			step: 'overview',
			stepping: false
		};
	}

	componentDidMount() {
		// scrollIntoView(document.getElementById('home') as any);
	}

	takeToLink = (id) => {
		scrollIntoView(document.getElementById(id) as any, { align: { top: 0.09, left: 0 } });
		this.setState({ stepping: true, step: id });
	};

	isVisible = ({ visible, directionY }, step) => {
		const { step: activeStep, stepping } = this.state;
		if (!stepping) {
			if (visible && activeStep !== step && directionY !== 'down') {
				this.setState({ step });
			} else if (!visible) {
				if (directionY === 'down') {
					const currentStep = steps.findIndex((item) => item.id === activeStep) || 0;
					const stepName = get(steps[currentStep - 1], 'id', 'overview');
					this.setState({ step: stepName });
				}
			}
		} else {
			this.setState({ stepping: false });
		}
	};

	QontoStepIcon = (props: StepIconProps, id) => {
		const { classes } = this.props;
		const { active } = props;
		return (
			<div>
				{active ? (
					<FiberManualRecordIcon className={classes.smallFont} />
				) : (
					<span>
						<FiberManualRecordOutlinedIcon className={classes.smallFont} />
					</span>
				)}
			</div>
		);
	};

	getActiveStep = () => {
		const { step } = this.state;
		return steps.findIndex((item) => item.id === step);
	};

	onSlideClick = () => {
		return <></>
	}

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes,isDesktopView } = this.props;
		return (
			<>
				{isDesktopView ? <div className={classes.content}>
						<DesktopCard name="Social Outreach" image="/images/social-desktop-hero.png" />
						<div className={classes.block}>
							<p
								className={clsx(
									classes.marginTop_8,
									classes.section,
									classes.smallFont
								)}
							>
								{Resources.SocialPara_1}
								<br />
								<br />
								{Resources.SocialPara_2}
								<br />
								<br />
								{Resources.SocialPara_3}
							</p>
						</div>
						<div className={classes.initiativeContainer}>
							<div className={classes.initiativeTitle}>
								<span></span>
								<FiberManualRecordIcon className={classes.dotStart}/>Ongoing Initiatives
								<FiberManualRecordIcon className={classes.dotEnd}/>
							</div>
						</div>
						<div className={classes.marginBT}>
							<ImageInfoBlocks contentUp="Improving Computer Literacy" contentDown="Reading Programme" image="/images/Carousel/Carousel_3.png" />
						</div>
						<div className={classes.marginBT}>
							<ImageInfoBlocks contentUp="Habits Of Cleanliness And Hygiene" contentDown="Building As a Learning Aid (BALA)" image="/images/Carousel/Carousel_1.png" leftContent/>
						</div>
						<div className={classes.marginBT}>
							<ImageInfoBlocks contentUp="Remedial Classes" contentDown="Public Private Partnership (PPP) Model Schools" image="/images/Carousel/Carousel_4.png" />
						</div>
						<div className={classes.marginBT}>
							<ImageInfoBlocks contentUp="Parent Outreach" contentDown="" image="/images/Carousel/Carousel_5.png" />
						</div>
						</div> : 
				<div className={classes.content}>
					<span id="overview" />
					<div id="home">
						<Card name="" designation="" image="/images/social.png" smallCard />
					</div>
					<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'overview')}>
						<div className={classes.block}>
							<div className={classes.largerFont}>
								{Resources.SocialOutreachTitle}
							</div>
							<div className={classes.fontSize_18}>
								{Resources.SocialOutreachSubtitle}
							</div>
							<p className={clsx(classes.section, classes.smallFont)}>
								{Resources.SocialPara_1}
								<br />
								<br />
								{Resources.SocialPara_2}
								<br />
								<br />
								{Resources.SocialPara_3}
							</p>
						</div>
					</VisibilityDetector>
					<div id="programmes" className={classes.marginTop_40}>
						<Carousel animation="slide"
							indicators={false}
							interval={5000}
							timeout={200}>
							{CarouselImages.map((item) => (
								<Card name="" designation="" image={item.image} smallCard />
							))}
						</Carousel>
					</div>
					<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'programmes')}>
						<div className={clsx(classes.marginTop_8, classes.block)}>
							<div className={classes.fontSize_22}>Ongoing Programmes</div>
							<p className={clsx(classes.section, classes.smallFont)}>
								{ProgrammesContent.map((item, i) => (
									<Tile item={item} />
								))}
							</p>
						</div>
					</VisibilityDetector>
					<div id="initiative" className={classes.marginTop_40}>
						<Carousel animation="slide"
							indicators={false}
							interval={5000}
							timeout={200}>
							{CarouselImages.map((item) => (
								<Card name="" designation="" image={item.image} smallCard />
							))}
						</Carousel>
					</div>
					<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'initiative')}>
						<div className={clsx(classes.marginTop_8, classes.block)}>
							<div className={classes.fontSize_22}>New Initiatives of 2019-20</div>
							<p className={clsx(classes.section, classes.smallFont)}>
								{InitiativesContent.map((item, i) => (
									<Tile item={item} />
								))}
							</p>
						</div>
					</VisibilityDetector>
				</div>
				}
				<div className={classes.stepperBlock}>
					<Stepper
						alternativeLabel
						activeStep={this.getActiveStep()}
						connector={<QontoConnector />}
						orientation="horizontal"
						classes={{ root: classes.stepperRoot }}
					>
						{steps.map((item) => (
							<Step key={item.id}>
								<StepLabel
									onClick={() => this.takeToLink(item.id)}
									StepIconComponent={(props) =>
										this.QontoStepIcon(props, item.id)
									}
									classes={{
										labelContainer: classes.stepperLabel,
										label: classes.alternativeLabel
									}}
								>
									{item.name}
								</StepLabel>
							</Step>
						))}
					</Stepper>
				</div>
				{isDesktopView && <Footer />}
			</>
		);
	}
}

const mapStateToProps = (state: RootState) => ({
	isDesktopView: state.screenReducer.isDesktopView
});

export default withStyles(Style, { name: 'DmartCorpFreshSocialOutreach' })(connect(mapStateToProps)(SocialOutreach));
